.BuildControls{
    width : 100%;
    background-color: rgb(240, 232, 214);
    display: flex;
    flex-flow: column;
    text-align: center;
    box-shadow: 0 2px 1px #ccc;
    margin: auto;
    padding: 10px 0;
}
.OrderButton {
    width: 150px;
    margin: 0 auto;
    background-color: rgb(200, 204, 168);
    outline: none;
    cursor: pointer;
    border: 1px solid #f9faf6;
    color: white;
    font-family: inherit;
    font-size: 1.2em;
    padding: 15px 30px;
    box-shadow: 2px 2px 2px #ccc;
}

.OrderButton:hover, .OrderButton:active {
    background-color: rgb(200, 195, 150);
    border: 1px solid #966909;
    color: #966909;
}

.OrderButton:disabled {
    background-color: #C7C6C6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888888;
}

.OrderButton:not(:disabled) {
    animation: enable 0.3s linear;
}

@keyframes enable {
    0% {
        transform: scale(1);
    }
    60% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}