.ContactData{
    margin: 20px auto;
    width: 80%;
    text-align: center;
    box-shadow: 0 2px 3px #ccc;
    border: 1px solid #eee;
    padding: 10px;
    box-sizing: border-box;
}
.ContactData form{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.Input{
    display: block;
    width: 300px;
    height: 2rem;
}